h1 {
  text-align: center;
  color: #4CAF50;
  margin-bottom: 40px;
  font-size: 54px;
}

.button-content {
  text-align: center;
}

.button-content button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

.button-content button:hover {
  background-color: #45a049;
}

.button-content .automatic-button {
  margin-left: 20px;
  background-color: #007BFF;
}

.button-content .automatic-button:hover {
  background-color: #0056b3;
}

#task-list {
  max-width: 600px;
  margin: 50px auto;
}

.task {
  background-color: white;
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.task:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.completed {
  text-decoration: line-through;
  color: #999;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.close-btn {
  float: right;
  font-size: 25px;
  cursor: pointer;
}

input, select {
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

button[type="submit"] {
  margin-top: 20px;
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

.task a {
  text-decoration: none;
  color: inherit;
}

.task a:hover {
  text-decoration: underline;
}



.complete-btn {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.complete-btn:hover {
  background-color: #0056b3;
}

.Completed {
  text-decoration: line-through;
  color: gray;
}

